<template>
  <div>
    <nav-head title="钻石明细" FixedOrTransparency="isFixed NoTransparency"/>
    <ul class="thead"><li>时间</li><li>事件内容</li><li>钻石变动</li></ul>
    <mt-loadmore
      :top-method="loadTop"
      :bottom-all-loaded="true"
      :autoFill="false"
      ref="loadmore"
      style="margin-top: 54px;"
    >
      <div
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
        infinite-scroll-immediate-check="false"
      >
        <div class="tbody">
          <div v-for="item in DiamondLog" v-bind:key="item.id">
            <span class="time">{{item.create_time | dateFormat('yyyy-MM-dd hh:mm:ss')}}</span>
            <span class="description">{{item.description}}</span>
            <span class="value" v-bind:class="{ 'color-green': item.diamond.indexOf('-') >= 0 }">{{item.diamond.indexOf('-') >= 0 ? item.diamond : "+"+item.diamond}}</span>
          </div>
        </div>
        <div v-show="loading!==1" style="color: #999;text-align: center;line-height: 35px;">
          加载中•••
        </div>
      </div>
    </mt-loadmore>
  </div>
</template>
<script>
import NavHead from '@/components/NavHead.vue'
import { Loadmore } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { GET_DIAMOND_LOG } from '../../../../vuex/Mine'

export default {
  name: 'diamondLog',
  data: function () {
    return {
      loading: null
    }
  },
  computed: {
    ...mapState({
      DiamondLog: state => state.mine.DiamondLog
    })
  },
  methods: {
    loadTop () {
      // 刷新数据
      this.getDiamondLog({
        index: 1,
        func: () => {
          setTimeout(() => {
            this.$refs.loadmore.onTopLoaded()
            if (this.loading) this.loading = false
            if (this.DiamondLog.length >= 10) {
              this.loadMore()
            } else {
              this.loading = 1
            }
          }, 500)
        }
      })
    },
    loadMore () {
      // 加载更多数据
      this.loading = true
      if (this.DiamondLog.length > 0 && this.DiamondLog.length % 10 !== 0) return
      const leng = this.DiamondLog.length
      this.getDiamondLog({
        index: 2,
        func: () => {
          if (leng !== this.DiamondLog.length && this.DiamondLog.length % 10 === 0) {
            // 长度变化且能被整除
            this.loading = false
          } else {
            // 长度没变拿不到新数据了,永远禁止上拉加载
            this.loading = 1
          }
        }
      })
    },
    ...mapActions({
      getDiamondLog: GET_DIAMOND_LOG
    })
  },
  created () {
    this.getDiamondLog({
      index: 1,
      func: () => {
        if (this.DiamondLog.length >= 10) {
          this.loadMore()
        } else {
          this.loading = 1
        }
      }
    })
  },
  components: {
    NavHead,
    [Loadmore.name]: Loadmore
  }
}
</script>
<style scoped lang="scss">
  ul.thead {
    position: fixed;
    width: 100%;
    background-color: $background-light-color;
    @if $IF365 { border-bottom: 1px solid $border-color; }
    @else { border-bottom: 2px solid $border-color; }
    height: 54px;
    line-height: 54px;
    overflow: hidden;
    z-index: 2;
    top: 46px;
    text-align: center;
    > li {
      display: inline-block;
      font-size: 15px;
      @if $IF365 { border-right: 1px solid $border-color; }
      @else { border-right: 2px solid $border-color; }
      float: left;
    }
    > li:nth-child(1) {
      width: 28%;
    }
    > li:nth-child(2) {
      width: 43%;
    }
    > li:last-child {
      width: 29%;
      border-right: none;
    }
  }

  div.tbody {
    background-color: $background-light-color;
    padding: 0 0;
    position: relative;
    text-align: center;
    div{
      height: 54px;
      line-height: 54px;
      border-bottom: 1px solid $border-color;
      overflow: hidden;
      span{
        display: inline-block;
        float: left;
        font-size: 14px;
      }
      span.time {
        width: 28%;
        @if $IF365 { color: #CCC; }
        @else { color: #999;}
        line-height: 16px;
        padding-top: 10px;
      }
      span.description {
        width: 43%;
      }
      span.value {
        width: 29%;
        @if $IF365 { color: $back-color; }
        @else { color: #ED3232;}
      }
      span.value.color-green {
        @if $IF365 { color: #FFF9E0; }
        @else { color: #40AC2E;}
      }
    }
  }
</style>
